body {
  background-color: #ccc;
}

#root {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding: 100px;
}

.toolbar {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 1);
  width: 100%;
  display: flex;
  justify-content: center;
}

.page-container {
  background-color: #ccc;
  padding-top: 46px;
}

