.button-square {
  display: block;
  min-width: 36px;
  height: 36px;
  background-color: transparent;
  float: left;
  box-sizing: border-box;
  margin: 5px 0 5px 3px;
  border-radius: 2px;
  padding: 0 8px;
  border: none;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
}

.button-square:hover {
  background-color: rgba(0,0,0,.05);
}

.button-square:active {
  color: #3498db;
}